@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,900;1,300;1,400;1,500;1,600;1,700;1,900&display=swap');

:root{
    --primary-color: #662d91;
        --secondary-color: #ed6991;
     font-family: 'Poppins', sans-serif;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html{
    scroll-behavior: smooth;
    height: 100%;
    overflow-x: hidden;
}

@media screen and (min-width:288px) and (max-width:1080px){
    body::-webkit-scrollbar{
        display: none;
    }
}